type FormData = {
  name: string
  email: string
  hours: number
}

export default function HomePage() {
  const [selectedBike, setSelectedBike] = useState<Bike | null>(null)
  const [submitted, setSubmitted] = useState(false)

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm<FormData>()

  const onSubmit = (data: FormData) => {
    if (selectedBike) {
      setSubmitted(true)
      reset()
    }
  }

  const handleSelectBike = (bike: Bike) => {
    setSelectedBike(bike)
    setSubmitted(false)
    reset()
  }

  const BikeDetail = dynamic(() => import("../components/BikeDetail"), { ssr: false })

  return (
    <div className="max-w-5xl mx-auto space-y-10">
      <section>
        <h2 className="text-2xl font-semibold mb-6">Available Bikes for Rent</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {bikes.map((bike) => (
            <Card
              key={bike.id}
              className={`cursor-pointer bg-gray-800 rounded-lg shadow-md overflow-hidden ${
                selectedBike?.id === bike.id ? "border-2 border-purple-600" : ""
              }`}
              onClick={() => handleSelectBike(bike)}
            >
              <img
                src={bike.image}
                alt={bike.name}
                className="w-full h-48 object-cover"
              />
              <CardHeader className="p-4">
                <div className="flex justify-between items-center">
                  <CardTitle className="text-white">{bike.name}</CardTitle>
                  <div className="flex items-center space-x-1 bg-purple-700 text-white rounded px-2 py-1 text-sm font-semibold">
                    <span>{bike.rating.toFixed(1)}</span>
                    <Star className="w-4 h-4 text-yellow-400" />
                  </div>
                </div>
                <CardDescription className="text-gray-300">{bike.location}</CardDescription>
              </CardHeader>
              <CardContent className="p-4 text-gray-300">
                <p>{bike.description}</p>
                <div className="mt-2 flex space-x-3">
                  {bike.features.map((feature, idx) => (
                    <div
                      key={idx}
                      className="flex items-center space-x-1 bg-gray-700 rounded px-2 py-1 text-xs"
                    >
                      {feature === "Wifi" && <Wifi className="w-4 h-4" />}
                      {feature === "Garage" && <Building className="w-4 h-4" />}
                      {feature === "3 habitaciones" && <Bed className="w-4 h-4" />}
                      {feature === "4 habitaciones" && <Bed className="w-4 h-4" />}
                      {feature === "2 habitaciones" && <Bed className="w-4 h-4" />}
                      <span>{feature}</span>
                    </div>
                  ))}
                </div>
              </CardContent>
              <CardFooter className="p-4 flex justify-between items-center text-white font-semibold">
                <span className="text-lg">${bike.pricePerHour} / hour</span>
                <Button variant="default" size="sm" onClick={() => handleSelectBike(bike)}>
                  Rent
                </Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      </section>

      {selectedBike && (
        <BikeDetail
          bike={selectedBike}
          onSubmit={onSubmit}
          register={register}
          errors={errors}
          handleSubmit={handleSubmit}
          disabled={!selectedBike}
        />
      )}
    </div>
  )
}
